import LoginBox from './LoginBox';
import React from 'react';
import Paper from 'material-ui/Paper';

const styles = {
  cardStyle: {
    maxWidth: '500px',
    overflow: 'hidden',
    margin: 'auto',
    display: 'flex'
  },
  decorationStyle: {
    flex: 1,
    background: '#8EC3E3',
    position: 'relative'
  }
};

const LoginCard = () => (
  <Paper style={styles.cardStyle} zDepth={5}>
    <div style={styles.decorationStyle}>
      <div
        style={{
          width: '3rem',
          height: '3rem',
          borderRadius: '50%',
          background: '#FFF',
          transform: 'translate(2rem, 4rem)'
        }}
      />
      <div
        style={{
          width: '3rem',
          height: '3rem',
          background: '#FFF',
          transform: 'translate(8rem, 10rem) rotate(45deg)'
        }}
      />
      <div
        style={{
          width: 0,
          height: 0,
          borderTop: '60px solid transparent',
          borderBottom: '60px solid transparent',
          borderLeft: '60px solid #FFF',
          transform: 'translate(8rem, -5rem) rotate(-45deg)'
        }}
      />
    </div>
    <LoginBox />
  </Paper>
);

export default LoginCard;



